/* header contain */
body {
    background-color: #e9ebee;
}

.fab {
    color: white;
    font-size: 30px;
    margin-right: 5px;
    margin-top: -1px;
}

/* header-search */

.header-search input[type="text"] {
    height: 25px;
    border: none;
    width: 80%;
}

.header-search button {
    border: none;
    height: 27px;
}


/* header-menu */
.header-menu a {
    color: white;
    text-decoration: none;

}

.header-menu {

    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    list-style-type: none;
    margin-top: 10px;


}


/* header-option */
.header-option a {
    color: #1a2947;
    text-decoration: none;
    justify-content: space-between;
}

.header-option i {
    color: #1a2947;
    font-size: 20px;
    margin-right: 10px;
}